    BLink,
    BNavbarNav,
    Locale,
    BNavItemDropdown,
    BDropdownItem,
    BDropdownDivider,
    BAvatar,

    // Navbar Components
    DarkToggler,
  },
  props: {
    toggleVerticalMenuActive: {
      type: Function,
      default: () => {},
    },
  },
  computed: {
    authUser() {
      return this.$store.getters['auth/userData']
    },
  },
  methods: {
    logout() {
      localStorage.removeItem('local')
      this.$store.dispatch('auth/Logout')
    },
  },
}
</script>
